import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Subject, Observable } from 'rxjs';

import { AppUser } from '../models/user.model';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private user: AppUser;
  private subject: Subject<AppUser>;

  constructor(private router: Router) {
    this.subject = new Subject();
    this.restoreUser();
  }

  private restoreUser(): void {
    const userData = JSON.parse(localStorage.getItem('_user'));

    if (userData == null)
    {
      this.setUser(null);
      return;
    }
    this.setUser(new AppUser(
      userData.id,
      userData.username,
      userData.email,
      userData.firstName,
      userData.lastName,
      userData.roleId,
      userData.statusId,
      userData.createdAt,
      userData.updatedAt,
      userData.token));
  }

  public get authenticated(): boolean {
    return this.user != null;
  }

  public get appUser(): AppUser {
    return this.user;
  }

  public get authenticatedUser(): Observable<AppUser> {
    return this.subject;
  }

  public setUser(user: AppUser): void {
    if (user != null)
    {
      localStorage.setItem('_user', JSON.stringify(user));
    }
    this.user = user;
    this.subject.next(user);
  }

  public logout(): void {
    this.user = null;
    this.subject.next(null);

    localStorage.clear();
  }
}
